import React, { Component, Fragment } from 'react';
import '../../asset/css/bootstrap.min.css';
import '../../asset/css/custom.css';

import {Container,Row,Col} from 'react-bootstrap'

class TotalMe extends Component {


    onScroll=()=>{
        if(window.scrollY>1120 && window.scrollY<1200){
            var newIn = setInterval(runNumber,10);
            var i=0;
            function runNumber(){
                i++;
                document.getElementById('tProject').innerHTML=i;
                document.getElementById('tClient').innerHTML=i;
                if(i>=100){
                    clearInterval(newIn);
                }
            }
        }
    }

    componentDidMount(){
        window.addEventListener('scroll',this.onScroll);
    }

    render() {
        return (
            <Fragment>
                <Container fluid={true} className='totalPoject p-0'>
                <div className='titleName text-center'></div>
                    <Row className="text-center projectCenter">
                        <Col lg={4} md={6} sm={12}>
                            <div className='Projects proSec'>
                                <h1 id='tProject'>100</h1>
                                <h4>TOTAL PROJECTS</h4>
                                <hr className='bLine'></hr>
                            </div>
                        </Col>
                        <Col lg={4} md={6} sm={12}>
                            <div className='Clients proSec'>
                                <h1 id='tClient'>100</h1>
                                <h4>TOTAL CLIENTS</h4>
                                <hr className='bLine'></hr>
                            </div>
                        </Col>
                        <Col lg={4} md={6} sm={12} className='cenWork'>
                            <div className='workProccess'>
                                <h4>How i Work-</h4>
                                <p><i className='fas fa-check-circle'></i> Requirments Gathering</p>
                                <p><i className='fas fa-check-circle'></i> System Analysis</p>
                                <p><i className='fas fa-check-circle'></i> Coding Testing</p>
                                <p><i className='fas fa-check-circle'></i> Implementation</p>
                            </div>                        
                        </Col>
                    </Row>
                </Container>
            </Fragment>
        );
    }
}

export default TotalMe;