import React, { Component, Fragment } from 'react';
import '../../asset/css/bootstrap.min.css';
import '../../asset/css/custom.css';
import {} from 'react-bootstrap';

import port1 from '../../asset/images/port1.jpg';
import port2 from '../../asset/images/port2.jpg';
import port3 from '../../asset/images/port3.png';


class Portfolio extends Component {
    render() {
        return (
            <Fragment>
                <div className='portContainer text-center'>
                    <h1 className='titleName'>Portfolio</h1>
                    <div className='portRow'>
                        <div className='portCard'>
                            <img src={port1} alt='port1'></img>
                            <h2>Card Name</h2>
                            <p>short Description short Descriptionshort Description short Description short Description</p>
                            <button className='btn btn-primary btn-sm'>Get Link</button>
                        </div>
                        <div className='portCard'>
                            <img src={port2} alt='port1'></img>
                            <h2>Card Name</h2>
                            <p>short Description short Descriptionshort Description short Description short Description</p>
                            <button className='btn btn-primary btn-sm'>Get Link</button>
                        </div>
                        <div className='portCard'>
                            <img src={port3} alt='port1'></img>
                            <h2>Card Name</h2>
                            <p>short Description short Descriptionshort Description short Description short Description</p>
                            <button className='btn btn-primary btn-sm'>Get Link</button>
                        </div>
                        <div className='portCard'>
                            <img src={port3} alt='port1'></img>
                            <h2>Card Name</h2>
                            <p>short Description short Descriptionshort Description short Description short Description</p>
                            <button className='btn btn-primary btn-sm'>Get Link</button>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}

export default Portfolio;