import React, { Component, Fragment } from 'react';
import { Carousel} from 'react-bootstrap';
import '../../asset/css/bootstrap.min.css';
import '../../asset/css/custom.css';

import clients1 from '../../asset/images/clients1.jpg';
import clients2 from '../../asset/images/clients2.jpg';
import clients3 from '../../asset/images/clients3.jpg';


class Slider extends Component {
    render() {
        return (
            <Fragment>
                <h1 className='titleName text-center'>CLIENTS SAYS</h1>
                <div className='sliderSec'>
                <Carousel>
                    <Carousel.Item className='slideBox'>
                        <div className='imgbox'>
                            <img src={clients1} alt='slid2'></img>
                        </div>                        <h4>First slide label</h4>
                        <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
                    </Carousel.Item>
                    <Carousel.Item className='slideBox'>
                        <div className='imgbox'>
                            <img src={clients2} alt='slid2'></img>
                        </div>
                        <h4>Second slide label</h4>
                        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                    </Carousel.Item>
                    <Carousel.Item className='slideBox'>
                        <div className='imgbox'>
                            <img src={clients3} alt='slid2'></img>
                        </div>                        <h4>Third slide label</h4>
                        <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
                    </Carousel.Item>
                </Carousel>
            </div>
            </Fragment>
        );
    }
}

export default Slider;