import React, { Component } from 'react';
import fire from '../config/fire';



class Login extends Component {
    constructor(props){


        super(props);
        this.login = this.login.bind(this);
        this.signup = this.signup.bind(this);
        this.handleEmail = this.handleEmail.bind(this);
        this.state={
            email:"",
            password:""
        }
    }

    login(e){
        e.preventDefault();
        fire.auth().signInWithEmailAndPassword(this.state.email, this.state.password).then((u)=>{
            console.log(u);
        }).catch((err)=>{
            console.log(err);
        })
    }
    signup(e){
        e.preventDefault();
        fire.auth().createUserWithEmailAndPassword(this.state.email,this.state.password).then((u)=>{
            console.log(u);
        }).catch((err)=>{
            console.log(err);
        })
    }

    handleEmail(e){
        this.setState({
            [e.target.name]:e.target.value
        })
    }

    
    render() {
        return (
            <div>
                {/* {this.state.user? (<Home/>) : ("Your are not Logged In !")} */}
                <div className='loginpage'>
                    <form>
                        <h3 class='logintitle'>Login Here</h3>
                        <label>Your Email</label><br></br>
                        <input type='email' name="email" onChange={this.handleEmail} value={this.state.email} placeholder='Email'></input><br></br>
                        <label>Your Password</label><br></br>
                        <input type='password' name='password'  onChange={this.handleEmail} value={this.state.password} placeholder='Password'></input><br></br>
                        <button onClick={this.login}>Login</button>
                        <button className="ml" onClick={this.signup}>SignUp</button>
                    </form>
                </div>
            </div>
        );
    }
}

export default Login;