import React, { Component,Fragment } from 'react';
import '../../asset/css/bootstrap.min.css';
import '../../asset/css/custom.css';
import {Container,Row} from 'react-bootstrap';
import PromoVideo from '../../asset/images/video.mp4';

class VideoSec extends Component {
    constructor(){
        super();
        this.state={
            videoAnim:'modal'
        }
    }
    onlick=()=>{
            this.setState({videoAnim:'ModalContainer'});
    }
    closeModal=()=>{
        document.getElementById('videopause').pause();
        this.setState({videoAnim:'modalCon'});
    }
    render() {
        return (
            <Fragment>
                <Container>
                    <Row>
                        <div className="text-center videosec">
                            <p>Revolution increased the available sources of food, and the invention of the wheel helped humans 
                            to travel in and control their simplest form of technology is the development and use of basic tools. The prehistoric 
                            discovery of how to control fire and the later Neolithic Revolution increased the available 
                            Internet, have lessened physical barriers to communication and allowed humans to</p>
                            <button onClick={this.onlick} className='playis' id='playButton'><i className='fas fa-play'></i></button>
                        </div>
                    </Row>
                </Container>
                <Container className={this.state.videoAnim} id='Modal'>
                    <Row>
                        <div className="videoModal">
                            <video controls id='videopause' className='promoVideo' src={PromoVideo}>
                                Your browser does not support the video tag.
                            </video>
                            <button onClick={this.closeModal} className='btn btn-primary btn-sm ml-2'>Close</button>
                        </div>
                    </Row>
                </Container>
            </Fragment>
        );
    }
}

export default VideoSec;