import React, { Component, Fragment } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import '../../asset/css/bootstrap.min.css';
import graphics from '../../asset/images/graphic.png';
import weblogo from '../../asset/images/website.png';
import android from '../../asset/images/android.png';


class Services extends Component {
    render() {
        return (
            <Fragment>
                <Container className='text-center'>
                    <h1 className='titleName'>MY SERVICES</h1>
                    <Row>
                        <Col lg={4} md={6} sm={12}>
                            <div className='serviceCard text-center'>
                                <img className='serviceslogo' src={graphics} alt='graphics'/>
                                <h2>Graphics Design</h2>
                                <p>Graphic design is a craft where professionals create visual content to communicate messages. By applying visual hierarchy and page layout techniques</p>
                            </div>
                        </Col>
                        <Col lg={4} md={6} sm={12}>
                            <div className='serviceCard text-center'>
                                <img className='serviceslogo' src={weblogo}alt='Web' />
                                <h2>Web Development</h2>
                                <p>Web development is the building and maintenance of websites; it's the work that happens behind the scenes to make a website look grea</p>
                            </div>                        
                        </Col>
                        <Col lg={4} md={6} sm={12}>
                            <div className='serviceCard text-center'>
                                <img className='serviceslogo' src={android} alt='Android'/>
                                <h2>Android Development</h2>
                                <p>Android software development is the process by which applications are created for devices running the Android operating system. </p>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </Fragment>
        );
    }
}

export default Services;