import React, { Component, Fragment } from 'react';
import '../asset/css/bootstrap.min.css';
import '../asset/css/custom.css';
import {NavLink} from 'react-router-dom';
import {Navbar, Nav} from 'react-bootstrap';
import fire from '../config/fire';

class NavBar extends Component {

    constructor(){
        super();
        this.state={
            navClass:"brandTitle",
            navBack:'navigationBar'
        }
    }

    onScroll=()=>{
        if(window.scrollY>100){
            this.setState({navClass:'brandTitleScroll', navBack:'navigationBarScroll'});
        }else if(window.scrollY<100){
            this.setState({navClass:'brandTitle', navBack:'navigationBar'})
        }
    }

    componentDidMount(){
        window.addEventListener('scroll', this.onScroll);
    }
    signout(){
        fire.auth().signOut();
    }


    render() {
        return (
            <Fragment>
                <Navbar className={this.state.navBack} fixed={'top'} collapseOnSelect expand="lg" bg="dark" variant="dark">
                    <Navbar.Brand className={this.state.navClass} href='/'>Shahriar Hussain</Navbar.Brand>
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="mr-auto"></Nav>
                        <Nav>
                            <Nav.Link><NavLink activeClassName='activeIt' exact to="/">HOME</NavLink></Nav.Link>
                            <Nav.Link><NavLink activeClassName='activeIt' exact to="/services">SERVICES</NavLink></Nav.Link>
                            <Nav.Link><NavLink activeClassName='activeIt' exact to="/course">COURSES</NavLink></Nav.Link>
                            <Nav.Link><NavLink activeClassName='activeIt' exact to="/portfolio">PORTFOLIO</NavLink></Nav.Link>
                            <Nav.Link><NavLink activeClassName='activeIt' exact to="/about">ABOUT</NavLink></Nav.Link>
                            <button onClick={this.signout} className="logoutbtn">LOGOUT</button>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
            </Fragment>
        );
    }
}

export default NavBar;